import Upscaler from 'upscaler';

import { CanvasSD } from './Core/CanvasSD';
import { Implementation } from './Implementation';
// import { Application } from '../';
export class AppUpscaler {
    // app: Application;
    canvasSD: CanvasSD;
    implementation: Implementation;
    host: HTMLElement;
    divText: HTMLElement;
    public static Init(id: string) {
        new AppUpscaler(id);
    }
    constructor(id: string = 'main') {

        // https://thekevinscott.github.io/UpscalerJS/#/?id=example

        // const target = document.getElementById('target');
        // const button = document.getElementById('button');
        // const info = document.getElementById('info');


        const path = 'https://raw.githubusercontent.com/NamjuLee/data/master/img/map/worldMapGlobeA_small.png';
        // const path = 'https://raw.githubusercontent.com/NamjuLee/data/master/img/building%20footprint_02.jpg'

        const img = new Image();

        img.src = path;

        const div = document.getElementById(id);

        if (div) {
            div.appendChild(img);
        }
        // button?.onclick = () => {
        //   info.innerText = 'Upscaling...';
        //   const start = new Date().getTime();

        // img.onload = () => {

        // };
        // };

        const upscaler = new Upscaler({
            // // model: 'div2k/rdn-C3-D10-G64-G064-x2',
            //   model: 'div2k-2x',
            // scale: 2,
        });
        upscaler.upscale(path).then((upscaledImgSrc) => {
            // console.log(upscaledImgSrc);
            const imgResult = document.createElement('img');
            imgResult.src = upscaledImgSrc as string;
            // // target.innerHTML = '';
            if (div) {
                div.appendChild(imgResult);
            }
            // const ms = new Date().getTime() - start;
            // info.innerText = `Upscaled in ${ms} ms`;
            // console.log('upscaler done');
        });





        // // this.app = app;
        // console.log('id: ' + id + ', App Init!!!!!');
        // const host = document.getElementById(id);
        // if (host) { this.host = host; }
        // this.InitHTML();
        // // this.threeCore = new ThreeCore();
        // // this.tfCore = new TFCore();
        // this.canvasSD = new CanvasSD(this);
        // this.implementation = new Implementation(this);
    }
    InitHTML() {
        const btn0 = document.createElement('button');
        btn0.style.width = '100px';
        btn0.style.height = '20px';
        btn0.style.marginLeft = '20px';
        btn0.style.zIndex = '5';
        // btn0.style.left = '20px';
        // btn0.style.top = '20px';
        // btn0.style.position = 'absolute';
        btn0.name = 'Clear';
        btn0.innerText = 'Clear';
        btn0.value = 'Clear';
        btn0.onclick = () => { console.log(this); this.canvasSD.Clear(); };

        this.host.appendChild(btn0);
        this.divText = document.createElement('div');
        this.divText.id = 'classText';
        this.divText.style.zIndex = '5';
        // this.divText.style.left = '150px';
        // this.divText.style.top = '20px';
        // this.divText.style.position = 'absolute';
        this.divText.innerText = 'class: ';
        this.divText.style.marginLeft = '20px';
        this.host.appendChild(this.divText);
    }
    Init(ctx: CanvasRenderingContext2D) {
        // console.log('this is init...');
    }
    Clear() {
        this.canvasSD.Clear();
    }
}

export class Solution {
    public divHost: HTMLElement;

    constructor(id: string ) {
        this.divHost = document.getElementById(id);

        AppUpscaler.Init(id)
    }
    public destroy() {
        if (this.divHost) {
            try {
                while (this.divHost.firstChild) {
                    this.divHost.removeChild(this.divHost.firstChild);
                }
            } catch (error) {
                console.error('Error in destroy method:', error);
            }
        } else {
            console.warn('divHost is undefined in destroy method');
        }
    }
}